<template>
    <div class="m-faq">
        <MSectionTitle :title="FAQ" :face="false" />
        <div class="container-fluid">
            <div class="c-question-list">
                <div class="c-question-item" v-for="(question, index) in list" :key="index">
                    <div class="c-question-item--header" @click="toggle(index)">
                        <h6 class="c-question-item--title">{{ question.question }}</h6>
                        <Icon :class="['m-question-icon', question.isOpen && 'is-open']" icon="fa:angle-right"
                            color="#00e8f0" width="14" />
                    </div>
                    <div v-show="question.isOpen" class="c-question-item--answer">
                        <span class="c-question-item--answer-inner">
                            {{ question.answer }}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MSectionTitle from '@/pages/main/common/m-section-title.vue'
import { FAQ } from '@/utils/constant'
import { Icon } from '@iconify/vue2';
export default {
    components: { MSectionTitle, Icon },
    data() {
        return {
            FAQ,
            laseIndex: 0,
            list: [
                {
                    question: 'What is the difference between Msea.io and Opensea?',
                    answer: 'Essentially both are trading platforms! But Opensea is a separate NFT trading market. Msea not only provides NFT trading, but also helps Gamefi market props to be traded on the chain.',
                    isOpen: true,
                },
                {
                    question: 'What is the difference between Msea.io\'s NFT and Token?',
                    answer: 'Msea NFT holders will have priority in platform activities. Msea Token holders can enjoy the ability to reduce or exempt handling fees in platform transactions',
                    isOpen: false,
                },
                {
                    question: 'Does Msea.io have platform currency?',
                    answer: 'Msea will only disclose token-related information when the mainnet is online.',
                    isOpen: false,
                },
                {
                    question: 'Can you tell more details about MSEA Dao?',
                    answer: 'MSEA Dao members can participate in the development of the Msea.io platform. We will make the codebase public after the mainnet launch',
                    isOpen: false,
                },
                {
                    question: 'Did Msea.io circumvent US law?',
                    answer: 'Msea is registered in Switzerland',
                    isOpen: false,
                },
            ]
        }
    },
    methods: {
        toggle(index) {
            if (index == this.laseIndex) {
                const tar = this.list[index];
                tar.isOpen = !tar.isOpen;
                this.$set(this.list, index, tar);
            } else {
                const tar1 = this.list[this.laseIndex];
                const tar2 = this.list[index];

                tar1.isOpen = false;
                tar2.isOpen = true;
                this.$set(this.list, this.laseIndex, tar1);
                this.$set(this.list, index, tar2);
            }
            this.laseIndex = index;
        },
    }
}
</script>

<style scoped lang="scss">
.m-faq {
    padding: 40px 0 40px;
    text-align: center;

    .c-question-list {
        margin-top: 30px;
    }

    .c-question-item {
        margin-bottom: 10px;
    }

    .c-question-item--header {
        font-size: 30px;
        font-family: Gotham-Bold;
        font-weight: 1000;
        // color: #00E8F0;
        color: #69e6cc;
        padding: 18px 30px;
        background: #31394E;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
    }

    .m-question-icon {
        transition: transform 0.2s;

        &.is-open {
            transform: rotate(90deg);
        }
    }

    .c-question-item--title {
        margin: 0;
    }

    .c-question-item--answer {
        border: 2px solid #31394E;
        box-sizing: border-box;
        margin: 0;
        padding: 32px;
        text-align: left;
        font-size: 16px;
        font-family: Gotham-Book;
        font-weight: 300;
        color: #FFFFFF;
        line-height: 28px;

        .c-question-item--answer-inner {
            display: inline-block;
            // width: 60%;
        }
    }
}

@media screen and (max-width: 768px) {
    .m-faq {
        padding: 30px 0 20px;

        .c-question-list {
            width: 90%;
            margin: 30px auto;

            .c-question-item--header {
                padding: 12px 15px;
                font-size: 24px;
            }

            .c-question-item--answer {
                padding: 12px;
                font-size: 14px;

                .c-question-item--answer-inner {
                    width: 100%;
                }
            }
        }
    }
}
</style>
