<template>
    <section id="m-header" :class="[isHidden ? 'is-hidden' : '', 'm-header']">
        <div class="container-fluid m-header-container">
            <img class="m-header-left m-header-logo" :src="mseaLogo" alt="MSEA" @click="backToTop" />
            <div class="m-header-right">
                <MMenu />
            </div>
        </div>
    </section>
</template>

<script>
import MMenu from './m-menu.vue'
import { scrollTo } from '@/utils'
import mseaLogo from '@/assets/images/msea-logo.png'

export default {
    components: {
        MMenu
    },
    data() {
        return {
            mseaLogo,
            top: 0,
            isHidden: false
        }
    },
    mounted() {
        this.listenScroll()
    },
    methods: {
        backToTop() {
            scrollTo(0, 200)
        },
        listenScroll() {
            window.addEventListener('scroll', () => {
                const currentTop = document.documentElement.scrollTop

                if (currentTop <= 200) {
                    // this.isHidden = false

                    this.top = currentTop

                    return
                }

                // if (currentTop > this.top) {
                //     this.isHidden = true
                // } else {
                //     this.isHidden = false
                // }

                this.top = currentTop
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.m-header {
    position: fixed;
    z-index: 9999;
    top: 0;
    width: 100vw;
    height: 84px;
    background: #131721;
    transition: top 0.1s;

    &.is-hidden {
        top: -84px;
    }

    .m-header-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
    }

    .m-header-left {
        height: 54px;
        cursor: pointer;
    }
}




@media screen and (min-width: 992px) and (max-width: 1200px) {
    .container-fluid {
        width: 100% !important;

        .m-header-logo {
            height: 34px;
            margin-top: -6px;
        }
    }
}

@media screen and (min-width: 1200px) {
    .container-fluid {
        width: 1080px;
    }
}

@media screen and (max-width: 768px) {
    .m-header {
        height: 60px;

        .m-header-logo {
            height: 34px;
            margin-top: -6px;
        }
    }
}
</style>
