<template>
    <div class="m-section-title">
        {{ uppercase(title) }}
        <!-- <img v-if="face" class="m-section-title-icon" :src="smileIcon" alt="" /> -->
    </div>
</template>

<script>
import smileIcon from '@/assets/images/icon/introduction-smile.png'
export default {
    name: 'section-title',
    props: {
        title: {
            type: String,
        },
        face: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            smileIcon
        }
    },
    methods: {
        uppercase(str) {
            return str.toUpperCase()
        }
    }
}
</script>

<style lang="scss" scoped>
.m-section-title {
    display: inline-block;
    border: 1px solid #69e6cc;
    border-radius: 48px;
    font-weight: bold;
    color: #69e6cc;
    font-family: Gotham-Bold;

    background: linear-gradient(180deg, #3cd8e0 0%, #89f0be 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.m-section-title-icon {
    width: 26px;
    vertical-align: baseline;
    object-fit: contain;
}

@media screen and (min-width: 1200px) {
    .m-section-title {
        padding: 15px 30px;
        font-size: 36px;
        line-height: 36px;

        .m-section-title-icon {
            width: 30px;
        }
    }
}

@media screen and (max-width: 1200px) and (min-width: 768px) {
    .m-section-title {
