<template>
    <div class="m-footer container">
        <div class="container-fluid">
            <img class="m-footer-left" v-lazy="mseaLogo" />
            <div class="m-footer-right">
                <span class="m-social-item">
                    <img class="m-social-icon" v-lazy="iconDiscord" alt="Use Discord to connect" @click="jump('discord')" />
                </span>
                <span class="m-social-item">
                    <img class="m-social-icon" v-lazy="iconTwitter" alt="Use Twitter to connect" @click="jump('twitter')" />
                </span>
                <span class="m-social-item">
                    <img class="m-social-icon" v-lazy="iconGithub" alt="Use Github to connect" @click="jump('github')" />
                </span>
                <span class="m-social-item">
                    <img class="m-social-icon" v-lazy="iconEmail" alt="Use E-mail to connect" @click="jump('email')" />
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import mseaLogo from '@/assets/images/msea-logo.png'
const iconGithub = require('@/assets/images/icon/social-github.png')
const iconTwitter = require('@/assets/images/icon/social-twitter.png')
// const iconFisher = require('@/assets/images/icon/social-fisher.png')
const iconEmail = require('@/assets/images/icon/social-email.png')
const iconDiscord = require('@/assets/images/icon/social-discord.png')

export default {
    data() {
        return {
            mseaLogo,
            iconGithub,
            iconTwitter,
            // iconFisher,
            iconDiscord,
            iconEmail
        }
    },
    methods: {
        jump (name) {
            switch (name) {
                case 'github':
                    window.open("https://github.com/MseaPlatform")
                    break;
                case 'twitter':
                    window.open("https://twitter.com/Gamefimsea")
                    break;
                case 'discord':
                    window.open("https://discord.gg/WMC8VKpZyV")
                    break;
                case 'email':
                    window.open("mailto:admin@msea.io")
                    break;
                default:
                    break;
            }
            
        }
    },
}
</script>

<style scoped lang="scss">
.m-footer {
    background: #0E121E;

}

.m-footer .container-fluid {
    padding: 40px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .m-footer-right {
        display: flex;
        align-items: center;
        padding-top: 10px;

        .m-social-item {
            margin-right: 25px;
            cursor: pointer;
        }

        .m-social-icon {
            width: 20px;
            height: 20px;
        }
    }
}

@media screen and (max-width: 768px) {
    .m-footer .container-fluid {
        padding: 30px 15px;

        .m-footer-left {
            width: 150px;
        }
    }
}
</style>
