<template>
    <div :class="['m-header-toggle', isExpand ? 'isExpand' : '']">
        <span class="m-header-toggle-button" @click="toggleMenu"></span>

        <div class="m-header-links">
            <div class="m-header-link" :key="id" @click="scrollToSection(link)" v-for="(link, id) in links">
                {{ uppercase(link) }}
            </div>
        </div>
    </div>
</template>

<script>
import { scrollTo } from '@/utils'
import { HashList } from '@/utils/constant'

export default {
    data() {
        return {
            links: HashList,
            isExpand: false
        }
    },
    methods: {
        uppercase(str) {
            return str.toUpperCase()
        },
        toggleMenu() {
            this.isExpand = !this.isExpand
        },
        scrollToSection(name) {
            const targetTop = document.getElementById(`section-${name}`).offsetTop
            const headerHeight = document.getElementById('m-header').offsetHeight;

            scrollTo(targetTop - headerHeight + 1, 200)


            this.toggleMenu()
        }
    }
}
</script>

<style lang="scss" scoped>
.m-header-toggle {
    position: relative;

    .m-header-links {
        display: flex;
        align-items: center;

        .m-header-link {
            font-weight: normal;
            color: #69E6CD;
            text-decoration: none;
            margin-right: 40px;
            cursor: pointer;

            &:last-child {
                margin-right: 15px;
            }

            &:hover {
                color: #fff;
            }
        }

        .m-connect-wallet {
            padding: 12px;
            border-radius: 20px;
            font-weight: 500;
            color: #131721;
            cursor: pointer;
            white-space: nowrap;
            background: linear-gradient(45deg, #3cd8e0, #89f0be);
        }
    }

    .m-header-toggle-button {
        transition: background-image 0.2s ease-in-out;
        cursor: pointer;
        display: none;
        width: 30px;
        height: 30px;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url(~@/assets/images/icon/menu.svg);
    }

    &.isExpand {
        .m-header-toggle-button {
            background-image: url(~@/assets/images/icon/close.svg);
        }

        .m-header-links {
            transform: translateX(0);
        }
    }
}

@media screen and (max-width: 992px) {
    .m-header-toggle-button {
        display: inline-block !important;
    }

    .m-header-links {
        position: fixed;
        width: 100vw;
        transition: transform 0.2s ease-in-out;
        flex-direction: column;
        transform: translateX(100vw);
        right: 0;
        top: 84px;
        background: #131721;
        padding: 20px 0;

        .m-header-link {
            margin-right: 0 !important;
            margin-bottom: 15px;
            font-family: Gotham-Book;

            &:last-child {
                margin-bottom: 20px;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .m-header-links {
        top: 60px;
    }
}
</style>
