<template>
    <div class="m-institutions">
        <div class="container-fluid">
            <MSectionTitle :title="INSTITUTIONS" :face="false" />
            <vue-seamless-scroll :data="list" :class-option="classOption" class="m-view-port">
                <div class="m-institution-list">
                    <img class="m-institution-item" :src="img.url" :alt="img.desc" v-for="(img, index) in list"
                        :key="index" />
                </div>
            </vue-seamless-scroll>
        </div>
    </div>
</template>

<script>
import vueSeamlessScroll from 'vue-seamless-scroll'
import inst1 from '@/assets/images/icon/inst1.png'
import inst2 from '@/assets/images/icon/inst2.png'
import i1 from '@/assets/images/insts/i1.jpg'
import i2 from '@/assets/images/insts/i2.jpg'
import i3 from '@/assets/images/insts/i3.jpg'
import i4 from '@/assets/images/insts/i4.jpg'
import i5 from '@/assets/images/insts/i5.jpg'
import i6 from '@/assets/images/insts/i6.jpg'
import i7 from '@/assets/images/insts/i7.jpg'
import i8 from '@/assets/images/insts/i8.jpg'
import { INSTITUTIONS } from '@/utils/constant'
import MSectionTitle from '@/pages/main/common/m-section-title.vue'

export default {
    components: {
        vueSeamlessScroll,
        MSectionTitle
    },
    data() {
        return {
            inst1,
            inst2,
            INSTITUTIONS,
            list: [],
            classOption: {
                limitMoveNum: 5,
                direction: 2,
                openTouch: true,
                step: 1
            }
        }
    },
        mounted() {
        this.list = new Array(16).fill(0).map((item, index) => {
            if (index % 8 === 0) {
                return {
                    url: i1,
                    desc: 'i1'
                }
            } else if(index % 8 === 1) {
                return {
                    url: i2,
                    desc: 'i2'
                }
            } else if(index % 8 === 2) {
                return {
                    url: i3,
                    desc: 'i3'
                }
            } else if(index % 8 === 3) {
                return {
                    url: i4,
                    desc: 'i4'
                }
            } else if(index % 8 === 4) {
                return {
                    url: i5,
                    desc: 'i5'
                }
            } else if(index % 8 === 5) {
                return {
                    url: i6,
                    desc: 'i6'
                }
            } else if(index % 8 === 6) {
                return {
                    url: i7,
                    desc: 'i7'
                }
            } else {
                return {
                    url: i8,
                    desc: 'i8'
                }
            }
        })
    }
}
</script>

<style scoped lang="scss">
.m-institutions {
    padding: 40px 0 40px;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;

    .m-view-port {
        width: 80%;
        margin: 50px auto 0;
        overflow: hidden;
    }

    .m-institutions-title {
        margin: 0;
        font-size: 50px;
        font-weight: bold;
        color: #69e6cc;
        background: linear-gradient(180deg, #3cd8e0 0%, #89f0be 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .m-institution-list {
        display: flex;
        margin-top: 20px;

        .m-institution-item {
            width: 70px;
            height: 70px;
            margin-right: 15px;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .container-fluid {
        width: 100vw;
    }
}

@media screen and (max-width: 768px) {
    .m-institutions {
        padding: 30px 0 20px;

        .container-fluid {
            width: 100vw;
        }

        .m-view-port {
            margin: 20px auto 0;
        }

        .m-institutions-title {
            font-size: 25px;
            line-height: 20px;
        }

        .m-item .m-item-icon {
            width: 100px;
            height: 60px;
            margin-right: 0;
        }
    }
}
</style>
